.LayoutArea {


    display: grid;
    height: 100vh;
    grid-template-rows: 15% 80% 5%;
    grid-template-columns: repeat(6, 1fr);

    &>header,
    &>footer {
        grid-column: span 6;
    }

    &>main {
        grid-column: span 5;
    }

    &>* {
        border: 1px solid black;
    }

}