.Loader {

    border:16px solid #f3f3f3; //light gray
    border-top:16px solid #3498db; // blue
    width:120px;
    height:120px;
    border-radius: 50%;
    animation:spin 2s linear infinite;

}

@keyframes spin {
    0%{
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}